<!--  -->
<template>
  <div class="main">
    <!-- <span class="title">设备日志</span> -->
    <el-table
      :data="tableData"
      border
      height="650"
      :header-cell-style="{
        'background-color': 'rgba(32, 39, 65, 0.7)',
        color: '#fff',
      }"
    >
      <el-table-column type="index" min-width="10%" align="center"> </el-table-column>
      <el-table-column prop="date" label="时间" min-width="20%" align="center">
      </el-table-column>
      <el-table-column prop="attackip" label="来源ip" min-width="20%" align="center">
      </el-table-column>
      <el-table-column prop="attackuser" label="来源用户" min-width="20%" align="center">
      </el-table-column>
      <el-table-column prop="targetdevice" label="目标设备" min-width="20%" align="center">
      </el-table-column>
      <el-table-column label="备注" min-width="23%" align="center" > 
        <el-button type="success">缓解成功</el-button>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      tableData: [],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    loadDeviceLog() {
      this.$axios
        .get(this.$httpUrl + "/devicelog")
        .then((res) => res.data)
        .then((res) => {
          console.log(res);
          this.tableData = res;
        });
    },
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.loadDeviceLog();
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
